import React from 'react'
import Main from './components/Main/Main'
import LoadingComponent from './components/LoadingComponent'
import './App.scss'
import logo from './img/zewa.png'

export default class App extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			loaded: false
		}
	}
	componentDidMount() {
		setTimeout(() => {
			this.setState({
				loaded: true
			})
		}, 1000)
	}
	render() {
		return (
			<div className="gameContainer">
				<nav className="row justify-content-between">
					<div className="col-lg-auto logo">
						<img src={logo} />
					</div>
					<div className="col-lg-auto delo">
						<span>Delo Z</span>
					</div>
				</nav>
				{this.state.loaded ? <Main /> : <LoadingComponent />}
			</div>
		)

	}
}